.pdf-page--certificate article {
  position: relative;
  text-align: center;
}

.pdf-page--certificate .page-title {
  font-size: 28pt;
  line-height: 1.2;
}

.pdf-page--certificate .certificate__banner {
  position: absolute;
  left: 1pc;
  bottom: 2pc;
  width: 8pc;
  height: 8pc;
  transform: rotate(-15deg);
}

.pdf-page--certificate .certificate__banner::before, .pdf-page--certificate .certificate__banner::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 8pc;
  height: 8pc;
  background-color: currentColor;
}

.pdf-page--certificate .certificate__banner::before {
  transform: rotate(-30deg);
}

.pdf-page--certificate .certificate__banner::after {
  transform: rotate(-60deg);
}

.pdf-page--certificate .certificate__banner .field--name-title {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 8pc;
  height: 8pc;
  font-size: 24pt;
  font-weight: bold;
  text-align: center;
  line-height: 8pc;
  color: #fff;
  z-index: 1;
}

.pdf-page--certificate .certificate__banner--gold {
  background-color: #e59e00;
}

.pdf-page--certificate .certificate__banner--gold::before, .pdf-page--certificate .certificate__banner--gold::after {
  background-color: #e59e00;
}

.pdf-page--certificate .certificate__banner--silver {
  background-color: #999999;
}

.pdf-page--certificate .certificate__banner--silver::before, .pdf-page--certificate .certificate__banner--silver::after {
  background-color: #999999;
}

.pdf-page--certificate .certificate__banner--bronze {
  background-color: #be3e1a;
}

.pdf-page--certificate .certificate__banner--bronze::before, .pdf-page--certificate .certificate__banner--bronze::after {
